// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription, of } from 'rxjs';
import { delay } from 'rxjs/operators';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
// CRUD
import { LayoutUtilsService, TypesUtilsService } from '../../../../../core/_base/crud';
// Services and Models
import { selectCustomersActionLoading } from '../../../../../core/e-commerce';
import { UserModel } from '../models/user.model';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../../globalVariables';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'users-edit-dialog',
	templateUrl: 'users-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class UsersEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	user: UserModel;
	userForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	// TODO: Get from API
	roles = [];
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<UsersReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<UsersEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private fb: FormBuilder,
		private store: Store<AppState>,
		private layoutUtilsService: LayoutUtilsService,
		private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.user = { ...this.data.reading, id: this.data.reading.userId };
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
		this.getRoles();
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}

		this.subscriptions.forEach(el => el.unsubscribe());
	}

	createForm() {
		let startDate = this.user.startDate;
		if (this.user.userId > 0 && startDate !== '-')
			startDate = new Date(+new Date(this.user.startDate) - this.tzoffset).toISOString().substr(0, 16);

		let endDate = this.user.endDate;
		if (this.user.userId > 0 && endDate !== '-')
			endDate = new Date(+new Date(this.user.endDate) - this.tzoffset).toISOString().substr(0, 16);

		this.userForm = this.fb.group({
			id: [this.user.userId, (this.user.userId === null || this.user.userId === undefined) ? '' : Validators.required],
			name: [this.user.name, Validators.required],
			// userName: [this.user.userName, Validators.required],
			// password: [this.user.password, this.user.userId > 0 ? '' : Validators.compose([
			// 	Validators.required,
			// 	Validators.minLength(8)
			// ])],
			roleId: [this.user.roleId, this.user.userId > 0 ? '' : Validators.required],
			// salary: [this.user.salary, Validators.required],
			isActive: [this.user.isActive, ''],
			startDate: [startDate, ''],
			endDate: [endDate, ''],
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.user.userId > 0) {
			return `تعديل المستخدم '${this.user.name}'`;
		}

		return 'اضافة مستخدم جديد';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.userForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */
	getRoles() {
		this.http.get(
			globalVariables.baseUrl + 'api/roles',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.roles = res.data;

			this.userForm.controls["roleId"].setValue(this.user.roleId);
		});
	}

	/**
	 * Returns prepared fuel price
	 */
	prepareReading(): UserModel {
		const controls = this.userForm.controls;
		const _user = new UserModel();
		_user.userId = this.user.userId;
		_user.name = controls.name.value;
		// _user.userName = controls.userName.value;
		// _user.password = controls.password.value;
		_user.roleId = controls.roleId.value;
		// _user.salary = controls.salary.value;
		_user.isActive = controls.isActive.value;
		_user.startDate = controls.startDate.value;
		_user.endDate = controls.endDate.value;

		return _user;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.userForm.controls;

		/** check form */
		if (this.userForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedReading = this.prepareReading();
		if (editedReading.userId > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update User
	 *
	 * @param _user: ShiftModel
	 */
	updateReading(_user: UserModel) {
		let startDate = null;
		let endDate = null;

		if (_user.startDate !== '-')
			startDate = new Date(+new Date(_user.startDate) - this.tzoffset).toISOString().slice(0, 16);

		if (_user.endDate !== '-')
			endDate = new Date(+new Date(_user.endDate) - this.tzoffset).toISOString().slice(0, 16);

		this.http.put(
			globalVariables.baseUrl + 'api/users',
			{
				..._user,
				// salary: +_user.salary,
				username: _user.userName,
				isActive: _user.isActive,
				// vacationsCountPerYear: 14,
				startDate,
				endDate
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _user, isEdit: true });
			}, err => {
				let msg = 'فشل في تعديل المستخدم';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create User
	 *
	 * @param _user: ShiftModel
	 */
	createReading(_user: UserModel) {
		this.http.post(
			globalVariables.baseUrl + 'api/users',
			{
				..._user,
				// salary: +_user.salary,
				username: _user.userName,
				// isActive: _user.isActive === "true",
				// vacationsCountPerYear: 14
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _user, isEdit: false });
			}, err => {
				let msg = 'فشل في اضافة المستخدم';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
