export class UserModel {
	userId: number;
	roleId: string;
	roleName: string;
	name: string;
	userName: string;
	password: string;
	salary: string;
	isActive: string;
	startDate: string;
	endDate: string;

	clear() {
		this.userId = 0;
		this.roleId = '';
		this.roleName = '';
		this.name = '';
		this.password = '';
		this.userName = '';
		this.salary = '';
		this.isActive = 'false';
		this.startDate = new Date().toISOString().substr(0, 16);
		this.endDate = new Date().toISOString().substr(0, 16);
	}
}
