// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription, of } from 'rxjs';
import { delay } from 'rxjs/operators';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
// CRUD
import { LayoutUtilsService, TypesUtilsService } from '../../../../../core/_base/crud';
// Services and Models
import { selectCustomersActionLoading } from '../../../../../core/e-commerce';
import { ShiftModel } from '../models/shift.model';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../../globalVariables';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'shifts-edit-dialog',
	templateUrl: 'shifts-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class ShiftsEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	shift: ShiftModel;
	shiftForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	date = new Date();
	tzoffset = (this.date).getTimezoneOffset() * 60000;
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<shiftsReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<ShiftsEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private fb: FormBuilder,
		private store: Store<AppState>,
		private layoutUtilsService: LayoutUtilsService,
		private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.shift = this.data.reading;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}

		this.subscriptions.forEach(el => el.unsubscribe());
	}

	createForm() {
		this.shiftForm = this.fb.group({
			id: [this.shift.id, Validators.required],
			name: [this.shift.name, Validators.required],
			isActive: [this.shift.isActive, this.shift.id > 0 ? Validators.required : ''],
			startDate: [this.shift.startDate, Validators.required],
			endDate: [this.shift.endDate, Validators.required],
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.shift.id > 0) {
			return `تعديل الشفت '${this.shift.name}'`;
		}

		return 'اضافة شفت جديد';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.shiftForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */

	/**
	 * Returns prepared fuel price
	 */
	prepareReading(): ShiftModel {
		const controls = this.shiftForm.controls;
		const _shift = new ShiftModel();
		_shift.id = this.shift.id;
		_shift.name = controls.name.value;
		_shift.isActive = controls.isActive.value;
		_shift.startDate = controls.startDate.value;
		_shift.endDate = controls.endDate.value;

		return _shift;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.shiftForm.controls;
		console.log(this.shift.id);
		console.log(this.shiftForm);

		/** check form */
		if (this.shiftForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update shift
	 *
	 * @param _shift: ShiftModel
	 */
	updateReading(_shift: ShiftModel) {
		// let startDate = new Date(+new Date(_shift.startDate) - this.tzoffset).toISOString().slice(0, 16);
		// let endDate = new Date(+new Date(_shift.endDate) - this.tzoffset).toISOString().slice(0, 16);

		this.http.put(
			globalVariables.baseUrl + 'api/shifts',
			{
				..._shift,
				isActive: _shift.isActive,
				startTime: _shift.startDate.length < 8 ? _shift.startDate + ':00' : _shift.startDate,
				endTime: _shift.endDate.length < 8 ? _shift.endDate + ':00' : _shift.endDate
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _shift, isEdit: true });
			}, err => {
				let msg = 'فشل في تعديل الشفت';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create shift
	 *
	 * @param _shift: ShiftModel
	 */
	createReading(_shift: ShiftModel) {
		// let startDate = new Date(+new Date(_shift.startDate) - this.tzoffset).toISOString().slice(0, 16);
		// let endDate = new Date(+new Date(_shift.endDate) - this.tzoffset).toISOString().slice(0, 16);

		this.http.post(
			globalVariables.baseUrl + 'api/shifts',
			{
				..._shift,
				isActive: _shift.isActive,
				startTime: _shift.startDate.length < 8 ? _shift.startDate + ':00' : _shift.startDate,
				endTime: _shift.endDate.length < 8 ? _shift.endDate + ':00' : _shift.endDate
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _shift, isEdit: false });
			}, err => {
				let msg = 'فشل في اضافة الشفت';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alert Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
