export class PumpModel {
	id: number;
	name: string;
	category: string;
	categoryId: number;
	isActive: string;
	creationDate: string;

	clear() {
		this.id = 0;
		this.category = '';
		this.categoryId = 0;
		this.name = '';
		this.isActive = 'false';
		this.creationDate = new Date().toISOString().substr(0, 16);
	}

	initialize() {
		this.id = 1;
		this.category = 'Gasoline';
		this.name = 'Z';
		this.isActive = 'false';
		this.creationDate = new Date().toISOString().substr(0, 16);
	}
}
