// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription, of } from 'rxjs';
import { delay } from 'rxjs/operators';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
// CRUD
import { LayoutUtilsService, TypesUtilsService } from '../../../../../core/_base/crud';
// Services and Models
import { selectCustomersActionLoading } from '../../../../../core/e-commerce';
import { PumpModel } from '../models/pump.model';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../../globalVariables';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'pumps-edit-dialog',
	templateUrl: 'pumps-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class PumpsEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	pump: PumpModel;
	pumpForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	// Get from API
	categories = [];
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<PumpsReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<PumpsEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private fb: FormBuilder,
		private store: Store<AppState>,
		private layoutUtilsService: LayoutUtilsService,
		private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.pump = this.data.reading;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
		this.getCategories();
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}

		this.subscriptions.forEach(el => el.unsubscribe());
	}

	createForm() {
		this.pumpForm = this.fb.group({
			id: [this.pump.id, Validators.required],
			name: [this.pump.name, Validators.required],
			category: [this.pump.categoryId, this.pump.id <= 0 ? Validators.required : ''],
			isActive: [this.pump.isActive, this.pump.id > 0 ? Validators.required : '']
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.pump.id > 0) {
			return `تعديل العداد '${this.pump.name}'`;
		}

		return 'اضافة عداد جديد';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.pumpForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */
	getCategories() {
		this.http.get(
			globalVariables.baseUrl + 'api/Categories?Type=fuel',
			{ headers: this.headers }
		).subscribe((res: any) => {
			this.categories = res.data.map(item => {
				return {
					name: item.categoryName,
					id: item.id
				}
			});

			this.pumpForm.controls["category"].setValue(this.pump.categoryId);
		});
	}

	/**
	 * Returns prepared fuel price
	 */
	prepareReading(): PumpModel {
		const controls = this.pumpForm.controls;
		const _pump = new PumpModel();
		_pump.id = this.pump.id;
		_pump.name = controls.name.value;
		_pump.categoryId = controls.category.value;
		_pump.isActive = controls.isActive.value;

		return _pump;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.pumpForm.controls;

		/** check form */
		if (this.pumpForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update Pumps
	 *
	 * @param _pump: PumpsModel
	 */
	updateReading(_pump: PumpModel) {

		this.http.put(
			globalVariables.baseUrl + 'api/GasolinePumps',
			{
				id: _pump.id,
				name: _pump.name,
				// categoryId:  _pump.categoryId,
				// currentReading: 1,
				isActive: _pump.isActive
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _pump, isEdit: true });
			}, err => {
				let msg = 'فشل في تعديل العداد';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create Pumps
	 *
	 * @param _pump: PumpsModel
	 */
	createReading(_pump: PumpModel) {
		this.http.post(
			globalVariables.baseUrl + 'api/GasolinePumps',
			{
				name: _pump.name,
				categoryId:  _pump.categoryId,
				// currentReading: 0,
				// isActive: _pump.isActive === "true" ? true : false
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _pump, isEdit: false });
			}, err => {
				let msg = 'فشل في اضافة العداد';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
