// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription, of } from 'rxjs';
import { delay } from 'rxjs/operators';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
// CRUD
import { LayoutUtilsService, TypesUtilsService } from '../../../../../core/_base/crud';
// Services and Models
import { selectCustomersActionLoading } from '../../../../../core/e-commerce';
import { SupplierModel } from '../models/supplier.model';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import globalVariables from '../../../globalVariables';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'supplier-edit-dialog',
	templateUrl: 'supplier-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class SuppliersEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	supplier: SupplierModel;
	supplierForm: FormGroup;
	hasFormErrors = false;
	viewLoading = false;
	// Private properties
	private componentSubscriptions: Subscription;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	// Store
	token = '';
	credentialsSubscription: Subscription = null;
	headers = null;
	requestOptions = {};

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<SuppliersReadingsEditDialogComponent>
	 * @param data: any
	 * @param fb: FormBuilder
	 * @param store: Store<AppState>
	 * @param layoutUtilsService
	 * @param http
	 */
	constructor(public dialogRef: MatDialogRef<SuppliersEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private fb: FormBuilder,
		private store: Store<AppState>,
		private layoutUtilsService: LayoutUtilsService,
		private http: HttpClient) {
	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.store.pipe(select(selectCustomersActionLoading)).subscribe(res => this.viewLoading = res);
		this.supplier = this.data.reading;
		this.createForm();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);
	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}

		this.subscriptions.forEach(el => el.unsubscribe());
	}

	createForm() {
		this.supplierForm = this.fb.group({
			id: [this.supplier.id, Validators.required],
			name: [this.supplier.name, Validators.required],
			mobileNumber: [this.supplier.mobileNumber, ''],
			telephone: [this.supplier.telephone, ''],
			type: [this.supplier.type.toLowerCase(), Validators.required],
			isActive: [this.supplier.isActive, this.supplier.id > 0 ? Validators.required : '']
		});
	}

	/**
	 * Returns page title
	 */
	getTitle(): string {
		if (this.supplier.id > 0) {
			return `تعديل المزود '${this.supplier.name}'`;
		}

		return 'اضافة المزود جديد';
	}

	/**
	 * Check control is invalid
	 * @param controlName: string
	 */
	isControlInvalid(controlName: string): boolean {
		const control = this.supplierForm.controls[controlName];
		const result = control.invalid && control.touched;
		return result;
	}

	/** ACTIONS */

	/**
	 * Returns prepared supplier price
	 */
	prepareReading(): SupplierModel {
		const controls = this.supplierForm.controls;
		const _supplier = new SupplierModel();
		_supplier.id = this.supplier.id;
		_supplier.name = controls.name.value;
		_supplier.mobileNumber = controls.mobileNumber.value;
		_supplier.telephone = controls.telephone.value;
		_supplier.type = controls.type.value;
		_supplier.isActive = controls.isActive.value;

		return _supplier;
	}

	/**
	 * On Submit
	 */
	onSubmit() {
		this.hasFormErrors = false;
		const controls = this.supplierForm.controls;

		/** check form */
		if (this.supplierForm.invalid) {
			Object.keys(controls).forEach(controlName =>
				controls[controlName].markAsTouched()
			);

			this.hasFormErrors = true;
			return;
		}

		const editedReading = this.prepareReading();
		if (editedReading.id > 0) {
			this.updateReading(editedReading);
		} else {
			this.createReading(editedReading);
		}
	}

	/**
	 * Update supplier price
	 *
	 * @param _supplier: supplierPricesModel
	 */
	updateReading(_supplier: SupplierModel) {
		this.http.put(
			globalVariables.baseUrl + 'api/Suppliers',
			{
				..._supplier,
				mobileNumber: _supplier.mobileNumber.toString(),
				telephone: _supplier.telephone.toString(),
				isActive: _supplier.isActive
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _supplier, isEdit: true });
			}, err => {
				let msg = 'فشل في تعديل المزود';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/**
	 * Create supplier price
	 *
	 * @param _supplier: supplierPricesModel
	 */
	createReading(_supplier: SupplierModel) {
		this.http.post(
			globalVariables.baseUrl + 'api/Suppliers',
			{
				..._supplier,
				mobileNumber: _supplier.mobileNumber.toString(),
				telephone: _supplier.telephone.toString(),
				// isActive: _supplier.isActive
			},
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				this.dialogRef.close({ _supplier, isEdit: false });
			}, err => {
				let msg = 'فشل في اضافة المزود';
				if (err.error) {
					msg = err.error.msg;
				}

				this.layoutUtilsService.showActionNotification(msg);
			});
	}

	/** Alect Close event */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}
}
