export class ShiftModel {
	id: number;
	name: string;
	isActive: string;
	startDate: string;
	endDate: string;

	clear() {
		this.id = 0;
		this.name = '';
		this.isActive = 'false';
		this.startDate = new Date().toISOString().substr(0, 16);
		this.endDate = new Date().toISOString().substr(0, 16);
	}
}
