export class SupplierModel {
	id: number;
	name: string;
	mobileNumber: string;
	telephone: string;
	type: string;
	isActive: string;
	creationDate: string;

	clear() {
		this.id = 0;
		this.name = '';
		this.mobileNumber = '';
		this.telephone = '';
		this.type = '';
		this.isActive = 'false';
		this.creationDate = new Date().toISOString().substr(0, 16);
	}
}
